<template>
  <div class="fullheight in-guide-cont">
    <div class="in-guide-pic" @click="open_click">开票软件</div>
    <div class="change-trade" style="display: none">切换企业</div>
    <!--登录对话框-->
    <div class="login" v-if="loginPop">
      <!--编号+x-->
      <div class="am-g login-parent">
        <!--关闭X-->
        <div class="am-u-lg-2 am-u-md-6 am-u-sm-6 login-x" @click="close_click">x</div>
      </div>
      <form>
        <!--用户名-->
        <div class="login-user">
          <select class="login-user-select" id="username" v-model="loginData.id">
            <option v-for="(item, i) in $store.state.user.userList" :key="i" :value="item.id">{{item.username}}</option>
          </select>
        </div>
        <!--密码-->
        <div class="login-pwd">
          <input
            type="password"
            class="login-input"
            id="pwd"
            v-model="loginData.password"
            autocomplete="false"
          />
        </div>
        <!--口令-->
        <div class="login-command">
          <input
            type="password"
            class="login-input"
            id="UPpwd"
            v-model="loginData.token"
            autocomplete="false"
          />
        </div>
        <!--更改口令-->
        <div
          class="login-change"
          id="change"
          style="background: rgb(255, 255, 255);"
        >更改证书口令
        </div>
        <!--记住密码-->
        <div class="login-remember">
          <input type="checkbox" class="login-checkbox"/>
          <button
            type="button"
            class="am-btn am-btn-primary am-radius"
            id="login-btn"
            @click="login"
          >登录
          </button>
        </div>
      </form>
      <div style="width:100%;position: absolute;top: 360px;text-align: center">默认密码为：123456；默认证书口令为：123456</div>
      <!--时间-->
      <div class="login-time">2016-2018</div>
    </div>

    <div ref="command_content" id="command-content" style="display: none">
      <!--证书密码修改部分-->
      <form>
        <div class="command-old">
          <input type="password" id="OriginalPwd" class="command-input" autocomplete="old"/>
        </div>
        <div class="command-new">
          <input type="password" id="aNewPwd" class="command-input" autocomplete="new"/>
        </div>
        <div class="command-new">
          <input type="password" id="bNewPwd" class="command-input" autocomplete="command"/>
        </div>
      </form>
      <div class="command-confirm">
        <button
          type="submit"
          class="am-btn am-btn-primary am-btn-xs am-radius"
          id="command-confirm"
        >确定
        </button>
        <button
          type="button"
          class="am-btn am-btn-primary am-btn-xs am-radius"
          id="command-cancel"
          @click="close"
        >取消
        </button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        loginPop: false,
        loginData: {
          id: '',
          password: '',
          token: '',
          username: '',
          status: 'normal'
        }
      }
    },
    created () {
      this.init()
    },
    mounted () {
    },
    methods: {
      init () {
        this.$store.commit('setCompanyId', 123)
      },
      open_click () {
        // const company = {
        //   trade_name: '问云教育科技有限公司',
        //   firm_code: '12321321321',
        //   province: '省份',
        //   user_name: '管理员',
        //   password: '123456',
        //   token: '123456',
        //   remark: '',
        //   industry: {
        //     industry_name: '123'
        //   },
        //   address: '我是企业地址',
        //   telno: '17663003502',
        //   bank_address: '中国工商银行淄博分行',
        //   bank_code: '123213213211',  //银行账号
        //   zhuan_code: '3700114140', //专票代码
        //   zhuan_code_start: '03279200', //专票开始号码
        //   zhuan_code_end: '03279250', //专票结束号码
        //   zhuan_xiane: '999999.99', // 专票限额
        //   po_code: '3700191160',  // 普票代码
        //   po_code_start: '05684500',  //普票号码开始
        //   po_code_end: '05684550', //普票好吗结束
        //   po_xiane: '999999.99',  //普票限额
        //   // kaipiao_time: '2019-10-01', //开票时间
        //   // lingpiao_time: '2019-10-01', //领票时间
        //   // lock_day: '2020-01-16', // 锁死日期
        //   // baoshui_day_start: '2019-12-10 00:00',
        //   // chaoshui_day_start: '2020-01-01',
        //   red_code_start: '661558640110161227158056',
        //   is_mini_company: '否',
        //   month: '2020-06',
        //   current_user: '郭锦'
        // }
        // this.$store.commit('setState', company)
        this.loginPop = true
      },
      close_click () {
        this.loginPop = false
      },
      editPassword () {
        // var _this = this
        layer.open({
          type: 1,
          title: ['证书口令修改', 'color:#ffffff;background-color:#315573;border-bottom:solid 1px #315573'],
          area: ['400px', '292px'], //宽高
          content: $('#command-content'),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5']   //遮罩阴影
        })
      },
      close () {
        layer.closeAll()
      },
      login () {
        let user = this.$store.state.user.userList.find(v => {
          return v.id = this.loginData.id
        })

        if (!this.loginData.password || !this.loginData.token) {
          this.$message.warning('请填写密码')
          return false
        }

        if (!this.loginData.token) {
          this.$message.warning('请填写口令')
          return false
        }

        if (user.password !== this.loginData.password || user.token !== this.loginData.token) {
          this.$message.warning('密码或口令填写错误')
          return false
        }

        this.loginData.username = user.username

        this.$store.commit('setUserData', this.loginData)
        this.$router.push('/index')
      }
    }
  }
</script>

